import {
  Dialog,
  DialogContent,
  makeStyles,
  Typography,
  Button,
  CircularProgress,
} from "@material-ui/core";

import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { setSellConfirm, setSellCreate } from "../../../../redux/reducers/sell";
import { getSellConfirm } from "../../../../redux/reducers/sell";

const SellConfirm = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const { answerCreateSell, answerSellConfirm } = useSelector(
    ({ SellReducer }) => SellReducer
  );
  const [loading, setLoading] = useState(false);
  const SellConfirm = () => {
    dispatch(getSellConfirm(answerCreateSell?.message?.confirm?.id));
    setLoading(true);
  };
  const firstUpdate = React.useRef(true);

  React.useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerSellConfirm.severity === 200) {
      setTimeout(() => {
        dispatch(setSellConfirm({ ...alert, open: false }));
        dispatch(setSellCreate({ ...alert, open: false, severity: null }));
      }, 500);
      setTimeout(() => {
        history.push("/dashboard/history");
      }, 1300);
    }
    if (answerSellConfirm.severity) {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [answerSellConfirm.severity]);

  return (
    <>
      <Dialog
        open={answerCreateSell.severity === 200}
        onClose={() =>
          dispatch(
            setSellCreate({
              ...alert,
              open: false,
              severity: null,
              message: answerCreateSell.message,
            })
          )
        }
        scroll="body"
        classes={{ paper: classes.paper }}
      >
        <DialogContent className={classes.DialogContent}>
          <Typography variant="h2" className={classes.title}>
            {t("paymentСonfirmation")}
          </Typography>
          <ul style={{ listStyle: "none" }}>
            <Typography variant="h5" component="li">
              {t("amountToBeCredited")}
              <div></div>
              <span>{answerCreateSell?.message?.confirm?.debit} USDT</span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("amountToPay")}
              <div></div>
              <span>
                {answerCreateSell?.message?.confirm?.amount}{" "}
                {answerCreateSell?.message?.confirm?.currency}
              </span>
            </Typography>
            <Typography variant="h5" component="li">
              {t("Commission")}
              <div></div>
              <span>
                {answerCreateSell?.message?.confirm?.commission} USDT
              </span>
            </Typography>
          </ul>
          <Button onClick={() => SellConfirm()} disabled={loading}>
            {" "}
            {loading ? (
              <CircularProgress className="progress" color="red" />
            ) : (
              t("confirm")
            )}
          </Button>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default SellConfirm;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
  },

  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },

    "& ul": {
      "& li": {
        fontWeight: 300,
        marginBottom: 16,
        display: "flex",
        justifyContent: "space-between",
        whiteSpace: "nowrap",
        alignItems: "baseline",

        "& div": {
          width: "100%",
          height: "fit-content",
          borderBottom: "2px dotted #B8C0CC",
          margin: "0 3px",
        },

        "& span": {
          fontWeight: 500,
        },
      },
    },

    "& button": {
      width: "100%",
      height: 56,
      marginTop: 16,
      background: "#FCE34D",
      borderRadius: "8px",
      border: "none",
      fontSize: 18,
      fontWeight: 500,
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
}));
