import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Container,
  Grid,
  makeStyles,
  Snackbar,
  TextField,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { WhiteBox } from "../../../../shared/Boxes";
import CommissionIcon from "../../../../assets/images/state/commissionDark.svg";
import CoinIcon from "../../../../assets/images/state/coin.svg";
import timeIcon from "./../../../../assets/images/state/timer.svg";
import { useParams } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import {
  CreateSell,
  getSellByParametersCoin,
  setSellCreate,
} from "../../../../redux/reducers/sell";
import SellConfirm from "../sellConfimr";
import { Alert } from "@material-ui/lab";
import NotFound from "../../../NotFound";
import Orders, { OrderAlert } from "../../../../components/Orders";
import Inputs from "../../../../components/Orders/Inputs";
import { createOrder } from "../../../../redux/reducers/orders";
import { useHistory } from "react-router-dom";
import Chart from "../../Buy/Chart";

const SellDetails = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { parameter } = useParams();
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.up("md"));
  const history = useHistory();

  const [order, setOrder] = useState("market");

  const { setSellByParameterCoin, answerCreateSell } = useSelector(
    ({ SellReducer }) => SellReducer
  );
  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const { order_data } = useSelector(({ orders }) => orders);

  useEffect(() => {
    dispatch(getSellByParametersCoin(parameter));
    //eslint-disable-next-line
  }, []);

  const SellDetailCoin = СurrenciesList.filter(
    (item) => item.currency === parameter
  )[0];
  const SellDetailUsdtCoin = СurrenciesList.filter(
    (item) => item.currency === "USDT"
  )[0];
  const [form, setForm] = useState({
    AmountToCredited: "",
    WriteOffAmount: "",
    price: SellDetailCoin?.rate,
  });
  const handleValueChange = ({ target: { value, name } }) => {
    setForm((form) => ({ ...form, [name]: value }));
  };
  const WriteOffAmountTotal = form.AmountToCredited * form?.price;
  const WriteOffAmountComm =
    (WriteOffAmountTotal * setSellByParameterCoin.parameters?.commission) / 100;
  const WriteOffAmountCalc = WriteOffAmountTotal - WriteOffAmountComm;

  const Create = () => {
    if (order !== "market") {
      const data = {
        order_type: order,
        order_side: "sell",
        currency: parameter,
        quantity: form.WriteOffAmountCalc,
        amount: form.AmountToCredited,
        price: form.price,
        stop_price: form.stop,
      };
      dispatch(createOrder(data));
    } else {
      const data = { sum: form.AmountToCredited, currency: parameter };
      dispatch(CreateSell(data));
    }
  };

  useEffect(() => {
    if (order_data.severity === 200)
      setTimeout(() => {
        history.push("/dashboard/orders");
      }, 1000);
    // eslint-disable-next-line
  }, [order_data]);

  useEffect(() => {
    if (order === "limit") {
      setForm({
        ...form,
        price: SellDetailCoin?.rate,
      });
    } else {
      setForm({
        ...form,
        price: SellDetailCoin?.rate,
        limit: SellDetailCoin?.rate,
      });
    }
    // eslint-disable-next-line
  }, [SellDetailCoin, order]);

  return (
    <>
      {setSellByParameterCoin.status === 400 ? (
        <NotFound />
      ) : (
        <section>
          <Snackbar
            autoHideDuration={1000}
            open={answerCreateSell.open}
            anchorOrigin={{ vertical: "top", horizontal: "center" }}
            onClose={() =>
              dispatch(
                setSellCreate({
                  ...alert,
                  open: false,
                  severity: answerCreateSell.severity,
                  message: answerCreateSell.message,
                })
              )
            }
          >
            <Alert
              onClose={() => dispatch(setSellCreate({ ...alert, open: false }))}
              severity={answerCreateSell.severity === 200 ? "success" : "error"}
              variant="filled"
            >
              {answerCreateSell.severity === 200
                ? ""
                : answerCreateSell.message && answerCreateSell.message.messages}
            </Alert>
          </Snackbar>
          <OrderAlert />
          <SellConfirm t={t} />
          {/* <Chart coin={SellDetailCoin} /> */}
          <Typography
            variant="h2"
            style={{ color: "#000", marginTop: 32, textAlign: "center" }}
          >
            {t("sell")} {SellDetailCoin?.name} {t("atRate")}
          </Typography>
          <Typography
            variant="h3"
            style={{
              color: "#64748B",
              textAlign: "center",
              fontWeight: 300,
              margin: "24px 0 56px",
            }}
          >
            1.00 {SellDetailCoin?.currency} = {SellDetailCoin?.rate} USDT
          </Typography>

          <Container>
            <Grid
              container
              spacing={2}
              justifyContent="center"
              style={{ marginTop: 40 }}
            >
              <Grid xs={12} sm={6} md={3} item>
                <WhiteBox style={{ height: 210, flexDirection: "column" }}>
                  <img className={classes.img} src={timeIcon} alt="time-icon" />
                  <Typography variant="h5" className={classes.SupTitle}>
                    {t("DueDate")}
                  </Typography>
                  <Typography variant="h4" className={classes.DetailsTitle}>
                    {t("Instantly")}
                  </Typography>
                </WhiteBox>
              </Grid>
              <Grid xs={12} sm={6} md={3} item>
                <WhiteBox style={{ height: 210, flexDirection: "column" }}>
                  <img className={classes.img} src={CoinIcon} alt="Coin-icon" />
                  <Typography variant="h5" className={classes.SupTitle}>
                    {t("MinAmount")}
                  </Typography>
                  <Typography variant="h4" className={classes.DetailsTitle}>
                    {setSellByParameterCoin.parameters?.min_qty}{" "}
                    {SellDetailCoin?.currency}
                  </Typography>
                </WhiteBox>
              </Grid>
              <Grid xs={12} sm={6} md={3} item>
                <WhiteBox style={{ height: 210, flexDirection: "column" }}>
                  <img
                    className={classes.img}
                    src={CommissionIcon}
                    alt="Commission-icon"
                  />
                  <Typography variant="h5" className={classes.SupTitle}>
                    {t("OurCommission")}
                  </Typography>
                  <Typography variant="h4" className={classes.DetailsTitle}>
                    {setSellByParameterCoin.parameters?.commission} %
                  </Typography>
                </WhiteBox>
              </Grid>
            </Grid>

            {/* <Orders t={t} setOrder={setOrder} order={order} /> */}

            <Grid style={{ marginTop: 40 }} container spacing={2}>
              <Grid item md={6} xs={12}>
                <Box
                  display="flex"
                  justifyContent="space-between"
                  style={{ flexDirection: !md && "column" }}
                >
                  {/* {md && (
                    <Typography
                      className={classes.DetailsTitle}
                      style={{ fontWeight: !md && 600 }}
                    >
                      {t("WriteOffAmount")}
                    </Typography>
                  )} */}
                  <Typography className={classes.DetailsTitle}>
                    <span style={{ fontWeight: 400 }}>{t("balance")}</span>:{" "}
                    <span
                      className={classes.balanceAmount}
                      style={{ cursor: "pointer" }}
                      onClick={() =>
                        setForm((form) => ({
                          ...form,
                          AmountToCredited: SellDetailCoin?.balance,
                        }))
                      }
                    >
                      {SellDetailCoin?.balance}
                    </span>{" "}
                    {SellDetailCoin?.currency}
                  </Typography>
                </Box>

                <Inputs
                  order={order}
                  classes={classes}
                  parameter={parameter}
                  t={t}
                  form={form}
                  handleValueChange={handleValueChange}
                />

                <Box display="flex">
                  <Box className={classes.DetailsLabel}>
                    {SellDetailCoin?.currency}
                  </Box>
                  <TextField
                    type="text"
                    InputProps={{
                      inputProps: {
                        pattern: "\\d*.\\d*",
                      },
                    }}
                    onKeyPress={(event) => {
                      if (!/[0-9.]/.test(event.key)) {
                        event.preventDefault();
                      }
                    }}
                    label={md ? "0.000000" : t("WriteOffAmount")}
                    variant="outlined"
                    name="AmountToCredited"
                    value={form.AmountToCredited}
                    className={classes.DetailsInput}
                    onChange={handleValueChange}
                  />
                </Box>
              </Grid>
              <Grid item md={6} xs={12}>
                <Box
                  display="flex"
                  justifyContent="space-between"
                  style={{ flexDirection: !md && "column" }}
                >
                  {/* {md && (
                    <Typography
                      className={classes.DetailsTitle}
                      style={{ fontWeight: !md && 600 }}
                    >
                      {t("AmountToCredited")}
                    </Typography>
                  )} */}
                  <Typography className={classes.DetailsTitle}>
                    <span style={{ fontWeight: 400 }}>{t("balance")}</span>:{" "}
                    {SellDetailUsdtCoin?.balance} USDT
                  </Typography>
                </Box>
                <Box display="flex">
                  <Box className={classes.DetailsLabel}>USDT</Box>
                  <TextField
                    label={md ? "0.000000" : t("AmountToCredited")}
                    name="WriteOffAmount"
                    variant="outlined"
                    className={classes.DetailsInput}
                    onChange={handleValueChange}
                    value={WriteOffAmountCalc.toFixed(
                      SellDetailUsdtCoin?.decimal
                    )}
                    disabled
                  />
                </Box>
              </Grid>
            </Grid>
            <Button
              variant="contained"
              size="large"
              color="primary"
              className={classes.button}
              onClick={() => Create()}
            >
              {t("Continue")}
            </Button>
          </Container>
        </section>
      )}
    </>
  );
};
const useStyles = makeStyles((theme) => ({
  SupTitle: {
    color: "#323A46",
    fontWeight: 300,
    margin: "22px 0 8px",
    [theme.breakpoints.down("md")]: {
      fontSize: 20,
    },
  },
  DetailsTitle: {
    fontWeight: 600,
    color: "#323A46",
    marginBottom: 16,
    fontSize: 23,
    [theme.breakpoints.down("md")]: {
      fontSize: 23,
    },
  },
  balanceAmount: {
    transition: "all .3s",
    cursor: "pointer",
    "&:hover": {
      borderBottom: "2px solid #000",
    },
  },
  DetailsLabel: {
    color: "#000000",
    fontWeight: 600,
    display: "flex",
    flexDirection: "column",
    alignItems: "center !important",
    justifyContent: "center",
    padding: "0 18px",
    backgroundColor: "#E7EAEE",
    borderRadius: "8px 0 0 8px",
    boxShadow: "0px 1px 0px #E2E8F0",
    minWidth: "70px",
  },

  button: {
    margin: "30px auto",
    display: "block",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
  DetailsInput: {
    "& .MuiOutlinedInput-input": {
      backgroundColor: "#fff",
      transition: "all 0.2s linear",
      borderRadius: "0 8px 8px 0!important",
    },

    "& .MuiOutlinedInput-notchedOutline": {
      borderRadius: "0 8px 8px 0!important",
    },
  },

  img: {
    width: 66,
    height: 66,
  },
}));
export default SellDetails;
